.message {
  margin: 5px 0;
  -ms-flex-item-align: left;
      align-self: left;
  background: var(--main-color);
  border-radius: 16px;
  color: #fff;
  font-family: Arial, Helvetica, sans-serif;
  padding: 5px 15px;
  -ms-flex-item-align: baseline;
      align-self: baseline;
  max-width: 225px;
  word-wrap: break-word;
}

.message__like {
  width: auto;
  height: 16px;
  padding-left: 10px;
  cursor: pointer;
}
.date {
  color: var(--secondary-text-color);
  font-size: 12px;
  text-align: right;
}



